import { Component, OnInit } from '@angular/core';
import { FormGroup ,FormBuilder,Validators } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import { DatacallService } from '../datacall.service';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  contactus:FormGroup;
  name;
  emailId;
  mobileNo;
  message;
  response;

  constructor( private datacalls: DatacallService,private fb: FormBuilder, private router: Router, public snackBar: MatSnackBar) { 
    this.contactus = this.fb.group({
      name : ['',Validators.required],
      emailId : ['',Validators.required],
      mobileNo : ['',Validators.required],
      message : ['',Validators.required]
  })
  }

  ngOnInit() {
  }
  Addcontact(data){
    console.log(data.value);
    this.datacalls.Addcontact(data.value).subscribe(
      res => {
        if (res.status == 200) {
          if (res.status == 200) {
            this.snackBar.open('Thanks You', 'Done', {
              duration: 4000,
            });
            setInterval(inv => {
              this.router.navigate(['/contact']);
            },3000);
          }
        }
      },
      err => {
        this.datacalls.errorHandler(err);
      }
    )
  }

}
