import faTrashAlt from '@fortawesome/fontawesome-free-regular/';
import { MatDialog } from '@angular/material';
import { DatacallService } from '../datacall.service';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import swal from 'sweetalert2';

declare var jquery: any;
declare var $: any;


@Component({
  selector: 'app-profile-detail',
  templateUrl: './profile-detail.component.html',
  styleUrls: ['./profile-detail.component.css']
})
export class ProfileDetailComponent implements OnInit {




  response;
  constructor(public dialog: MatDialog,private datacalls: DatacallService, private fb: FormBuilder, private router: Router, public snackBar: MatSnackBar) {

  }

  ngOnInit() {
    //console.log(jQuery().jquery);
  }


  openregisterDialog(): void {
    const dialogRef = this.dialog.open(PublicRegisterComponent, {
      width: '80%', height: '90%'

    });
  }

  openredregisterDialog(): void {
    const dialogRef = this.dialog.open(PublicredRegisterComponent, {
      width: '80%', height: '90%'

    });
  } 


}



@Component({
  selector: 'profile-detail',
  templateUrl: './profile-register.html'
})
export class PublicRegisterComponent implements OnInit {
  formreg: FormGroup;
  response;
  constructor(private datacalls: DatacallService, private fb: FormBuilder, private router: Router, public snackBar: MatSnackBar) {
    this.formreg = this.fb.group({
      name: ['', Validators.required],
      emailId: ['', Validators.required],
      mobileNo: ['', Validators.required],
      address: ['', Validators.required],
      country: ['', Validators.required],
      state: ['', Validators.required],
      city: ['', Validators.required],
      pincode: ['', Validators.required],
      membertype: ['6'],
      high_edu: ['', Validators.required],
      experience: ['', Validators.required],
      firmname: ['', Validators.required]
    })
  }


  ngOnInit() {
  }
  Registration() {
    var form = {
      "name": this.formreg.value.name,
      "emailId": this.formreg.value.emailId,
      "mobileNo": this.formreg.value.mobileNo,
      "address": this.formreg.value.address,
      "country": this.formreg.value.country,
      "state": this.formreg.value.state,
      "city": this.formreg.value.city,
      "pincode": this.formreg.value.pincode,
      "membertype": this.formreg.value.membertype,
      "high_edu": this.formreg.value.high_edu,
      "experience": this.formreg.value.experience,
      "firmname": this.formreg.value.firmname,
    }
    this.datacalls.Addregister(form).subscribe(
      res => {
        if (res.status == 200) {
          if (res.status == 200) {
            swal(
              'Thanks for registering with us.',
              'Our business development executive will get in touch with you within 1-2 working days.',
              'success'
            )
            // swal({
            //       type: 'error',
            //       title: 'Oops...',
            //       text: 'Thanks for registering with us. Our business development executive will get in touch with you within 1-2 working days.',
            //     })
            // this.snackBar.open('Thanks for registering with us. Our business development executive will get in touch with you within 1-2 working days.', 'Done', {
            //   duration: 4000,
            // });
            setInterval(inv => {
              this.router.navigate(['./']);
            },3000);
          }
        }
      },
      err => {
        this.datacalls.errorHandler(err);
      }
   
  )
  }


}


