@Component({
  selector: 'side-bar-register-dialog',
  templateUrl: './side-bar-register-dialog.html'
})
export class SideBarRegisterComponentDialog implements OnInit {
  signupreg: FormGroup;
  response;
constructor( private datacalls: DatacallService,private fb: FormBuilder , private router: Router, public snackBar: MatSnackBar){
  
  this.signupreg = this.fb.group({
    name: ['', Validators.required],
    emailId: ['', Validators.required],
    mobileNo: ['', Validators.required],
    address: ['', Validators.required],
    country: ['', Validators.required],
    state: ['', Validators.required],
    city: ['', Validators.required],
    pincode: ['', Validators.required],
    membertype: ['', Validators.required],
    high_edu: ['', Validators.required],
    experience: ['', Validators.required],
    firmname: ['', Validators.required]
  })

}
  ngOnInit() {
  }

  submit() {
    var form = {
      "name": this.signupreg.value.name,
      "emailId": this.signupreg.value.emailId,
      "mobileNo": this.signupreg.value.mobileNo,
      "address": this.signupreg.value.address,
      "country": this.signupreg.value.country,
      "state": this.signupreg.value.state,
      "city": this.signupreg.value.city,
      "pincode": this.signupreg.value.pincode,
      "membertype": this.signupreg.value.membertype,
      "high_edu": this.signupreg.value.high_edu,
      "experience": this.signupreg.value.experience,
      "firmname": this.signupreg.value.firmname,
    }
    console.log(form);
    this.datacalls.Addregister(form).subscribe(
      res => {
        if (res.status == 200) {
          if (res.status == 200) {
            this.snackBar.open('Thanks for registering with us. Our business development executive will get in touch with you within 1-2 working days.', 'Done', {
              duration: 4000,
            });
            setInterval(inv => {
              this.router.navigate(['/profile']);
            },3000);
          }
        }
      },
      err => {
        this.datacalls.errorHandler(err);
      }
   
  )
  }


}
